import React, { Component } from "react";
import InputDate from "../Components/inputDate";
import { getAllCountries } from "../Services/travelCosts";
import Option from "../Components/selects";
import { allNBPCurrency } from "../Services/servicurrency";
import Form from "../Components/Form";
import Expense from "./expenditure";
import ExpenseEditor from "./expenseEditor";
export default class Highorder extends Component {
  constructor(props) {
    super(props);
    this.state = {
      allCountries: [],
      selectValue: "pln",
      startDate: new Date().toISOString().substr(0, 10),
      startTime: new Date().toISOString().substr(11, 5),
      startDateAbroad: new Date().toISOString().substr(0, 10),
      startTimeAbroad: new Date().toISOString().substr(11, 5),
      stopDate: new Date().toISOString().substr(0, 10),
      stopTime: new Date().toISOString().substr(11, 5),
      stopDateAbroad: new Date().toISOString().substr(0, 10),
      stopTimeAbroad: new Date().toISOString().substr(11, 5),
      bid: 1,
      diet: 0,
      accommodation: 0,
      country: {},
      countryData: {},
      currency: "PLN",

      name: "Jaroslaw",
      surname: "Lucjan",
      rodzaj_wydatku: "No transport",
      kasa: 0,
      test_Array: [1, 2],
      expenses: [],
      tempDeleteIndex: 0,
      showStyle: { visibility: "hidden" },
    };
  }
  addExpense = (name) => {
    let tempExpenses = [...this.state.expenses];
    console.log("Funkcja przeslana z parametrem name: ", name);
    tempExpenses.push(name);
    this.setState({ expenses: tempExpenses });
  };
  deleteExpense = (event) => {
    let objectID = event.target.id;
    let tempArray = this.state.expenses;
    tempArray.splice(objectID, 1);
    this.setState({ expenses: tempArray });
  };

  showEditor = () => {
    this.setState({ showStyle: styleButton2 });
  };
  hideEditor = () => {
    this.setState({ showStyle: styleButton1 });
  };

  handleStateChange = (event) => {
    let name = event.target.name;
    var cc = event.target.value;
    this.setState({ [name]: cc });
  };
  componentDidMount() {
    getAllCountries().then((country) => {
      this.setState({ allCountries: country });
      console.log(this.state.allCountries);
    });
    allNBPCurrency(this.state.selectValue).then((element) => {
      console.log("Currency get from BNP", element);
      this.setState({ bid: element.rates[0].bid });
    });
  }

  // Zmiana w dropdown liscie
  handleChange = (event) => {
    var cc = event.target;
    var test = new Promise(function (resolve, reject) {
      console.error("Sprawdzam teraz tablice wszystkich krajw");
      resolve("First promise done");
    });
    test
      .then((result) => {
        const foundCountry = this.state.allCountries.find((element) => {
          if (cc.value === element.code) {
            console.log("Znalazlem element w tablicy!", element);
            return element;
          }
        });
        this.setState({ country: foundCountry });
        return foundCountry;
      })
      .then((result) => {
        console.log("A tutaj w drugim then: ", result);
        this.setState({ diet: result.value });
        if (result.code !== "pl") {
          allNBPCurrency(result.currency).then((element) => {
            console.log("Data from NBP:", element);
            this.setState({
              bid: element.rates[0].bid,
              currency: result.currency,
            });
          });
        } else {
          this.setState({
            bid: 1.0,
            currency: "PLN",
          });
        }
      });
  };

  testHandleDiffDate = () => {
    // To set two dates to two variables

    // to jest dla delegacji polskiej
    let data1 = this.state.startDate;
    let time1 = this.state.startTime;
    let data2 = this.state.stopDate;
    let time2 = this.state.stopTime;
    var date1 = new Date(data1 + " " + time1 + ":00");
    var date1a = new Date(data1 + " 23:59:59");
    var date2a = new Date(data2 + " 00:00:00");
    var date3a = new Date(data2 + " " + time2 + ":00");

    var Difference_In_Time1 = date1a.getTime() - date1.getTime();
    var Difference_In_Days1a = (
      Difference_In_Time1 /
      (1000 * 3600 * 24)
    ).toFixed(2);
    console.warn("Roznica czasu to: ", Difference_In_Days1a);

    var Difference_In_Time2 = date2a.getTime() - date1a.getTime();
    var Difference_In_Days2a = (
      Difference_In_Time2 /
      (1000 * 3600 * 24)
    ).toFixed(0);
    console.warn("Roznica czasu to: ", Difference_In_Days2a);
    var Difference_In_Time3 = date3a.getTime() - date2a.getTime();
    var Difference_In_Days3a = (
      Difference_In_Time3 /
      (1000 * 3600 * 24)
    ).toFixed(2);
    console.warn("Roznica czasu to: ", Difference_In_Days3a);
    if (Difference_In_Days1a >= 0.33 && Difference_In_Days1a <= 0.5) {
      console.log("Przysluguje 0.5 diety");
    } else if (Difference_In_Days1a > 0.5) {
      console.log("Przysluguje cala dieta");
    } else {
      console.log("Nie Przysluguje dieta");
    }
  };

  testHandleDiffDateAbroad = () => {
    let dateStartPL = this.state.startDate;
    let timeStartPL = this.state.startTime;
    let dateStartAbroad = this.state.startDateAbroad;
    let timeStartAbroad = this.state.startTimeAbroad;
    let dateStopAbroad = this.state.stopDateAbroad;
    let timeStopAbroad = this.state.stopTimeAbroad;
    let dateStopPL = this.state.stopDate;
    let timeStopPL = this.state.stopTime;
    // Pierwsza część:
    let timestartPL = new Date(dateStartPL + " " + timeStartPL + ":00");
    let timeStartPLAbroad = new Date(
      dateStartAbroad + " " + timeStartAbroad + ":00"
    );
    // time  in poland befor depature:
    let diff_1 = timeStartPLAbroad.getTime() - timestartPL.getTime();
    var Difference_1 = (
      diff_1 /
      (1000 * 3600 * 24)
    ).toFixed(2);
    console.log("Czas 1: ",timestartPL)
    console.log("Czas 1: ",timeStartPLAbroad)
    console.warn("Roznica czasu to: ", Difference_1);
    // Druga część: wylot do konca dnia i tutaj powinno byc do konca podrozy ewentualnie
    let time2_1 = new Date(dateStartAbroad + " " + "23:59:59");
    let diff_2 = time2_1.getTime() - timeStartPLAbroad.getTime();
    var difference_2 = (
      diff_2 /
      (1000 * 3600 * 24)
    ).toFixed(2);
    console.warn("Roznica czasu to: ", difference_2);
    // // Trzecia część: pełne dni za granicą
    let time3_1 = new Date(dateStopAbroad + " " + "00:00:00");
    let diff_3 = time3_1.getTime() - time2_1.getTime();
    var difference_3 = (
      diff_3 /
      (1000 * 3600 * 24)
    ).toFixed(2);
    console.warn("Roznica czasu to: ", difference_3);
    // // Czwarta część: pełne dni za granicą
    let time4_1 = new Date(dateStopAbroad + " " + timeStopAbroad+":00");
    let diff_4 = time4_1.getTime() - time3_1.getTime();
    var difference_4 = (
      diff_4 /
      (1000 * 3600 * 24)
    ).toFixed(2);
    console.warn("Roznica czasu to: ", difference_4);
    
    // // Piąta część:do przyjazdu z lotniska

    let time5_1 = new Date(dateStopPL + " " + timeStopPL+":00");
    let diff_5 = time5_1.getTime() - time4_1.getTime();
    var difference_5 = (
      diff_5 /
      (1000 * 3600 * 24)
    ).toFixed(2);
    console.log(time5_1);
    console.log(time4_1);
    console.warn("Roznica czasu to: ", difference_5);
  };

  handleInputStartDate = (event) => {
    this.setState({ startDate: event.target.value });
  };
  render() {
    if (this.state.allCountries.length === 0) {
      return <div>Lol pusto teraz</div>;
    } else {
      const CountriesSelect = this.state.allCountries.map((element) => {
        if (element.code === "pl") {
          return (
            <Option
              code={element.code}
              country={element.country}
              selected="selected"
              country={element.country}
            />
          );
        }
        return <Option code={element.code} country={element.country} />;
      });

      const expensesArray = this.state.expenses.map((element, index) => {
        return (
          <div>
            {" "}
            typ: {element.type}, data: {element.date}, kwota: {element.amount}{" "}
            zł
            <button id={index} onClick={this.deleteExpense}>
              Usuń
            </button>{" "}
            <button id={index} onClick={this.deleteExpense}>
              Edit
            </button>
          </div>
        );
      });

      return (
        <div className="container-contact">
          <h1>Lista wydatków:</h1>
          {expensesArray}
          <br />
          <button onClick={this.showEditor}>Dodaj wydatek</button>
          <button onClick={this.testHandleDiffDate}>Sprewadz date</button>
          <button onClick={this.testHandleDiffDateAbroad}>Sprewadz date calosc</button>
          <br />
          <ExpenseEditor
            addEvent={this.addExpense}
            style={this.state.showStyle}
            hideEditor={this.hideEditor}
          />
          Krajowy
          <InputDate
            nameDate="startDate"
            nameTime="startTime"
            handleChange={this.handleStateChange}
            max={new Date().toISOString().substr(0, 10)}
            placeHolderTime={this.state.startTime}
          />
          Krajowy to data: {this.state.startDate} czas: {this.state.startTime}
          <InputDate
            nameDate="stopDate"
            nameTime="stopTime"
            handleChange={this.handleStateChange}
            minDate={this.state.startDate}
            minTime={this.state.startTime}
            placeHolderTime={this.state.stopTime}
          />
          Krajowy to data: {this.state.stopDate} czas: {this.state.stopTime}
          Krajowy
          <InputDate
            nameDate="startDateAbroad"
            nameTime="startTimeAbroad"
            handleChange={this.handleStateChange}
            max={new Date().toISOString().substr(0, 10)}
            placeHolderTime={this.state.startTimeAbroad}
          />
          Zagraniczne to data: {this.state.startDateAbroad} czas:{" "}
          {this.state.startTimeAbroad}
          <InputDate
            nameDate="stopDateAbroad"
            nameTime="stopTimeAbroad"
            handleChange={this.handleStateChange}
            minDate={this.state.startDateAbroad}
            minTime={this.state.startTimeAbroad}
            placeHolderTime={this.state.stopTimeAbroad}
          />
          zagraniczne to data: {this.state.stopDateAbroad} czas:{" "}
          {this.state.stopTimeAbroad}
          <br />
          <select id="selColor" onChange={this.handleChange}>
            {CountriesSelect}
          </select>
          <h1>Sprawdzwamy co nam tutaj przyszl oz kraju:</h1>
          <p>currency: {this.state.currency}</p>
          <p>bid: {this.state.bid}</p>
          <p>diet: {this.state.diet}</p>
          <p>country: {this.state.country.code}</p>
          <p>countryData: {this.state.countryData.code}</p>
          <p>accommodation: {this.state.accommodation}</p>
          <p>Nazwa</p>
          <p>Nazwa</p>
          <h3>
            Nazwisko: {this.state.surname} <br />
            Imie: {this.state.name}
          </h3>
          <Form
            name="name"
            surname="surname"
            handleStateChange={this.handleStateChange}
          />
          <p>Sprawdzamy jak wystawic i dodac wydatki:</p>
          <Expense
            name="kasa"
            handleStateChange={this.handleStateChange}
            onChangeList={this.handleStateChange}
            objName="rodzaj_wydatku"
          />
          <div style={divStyle}>
            Imie: {this.state.name}
            <br />
            Nazwisko: {this.state.surname}
            <br />
            Wydatek: {this.state.kasa}
            <br />
            Rodzaj wydatku:: {this.state.rodzaj_wydatku}
            <br />
          </div>
        </div>
      );
    }
  }
}
const divStyle = {
  color: "yellow",
  background: "red",
  width: "100%",
  height: "100px",
};

const styleButton1 = {
  visibility: "hidden",
};
const styleButton2 = {
  visibility: "visible",
};
